
.board, .board-cell {
  border-collapse:collapse;
  border: 1px solid white;
  color: white;
}

.board {
  background-color: black;
  opacity: 0.7;
  width: 430px;
  height: 430px;
  margin: auto;
  margin-top: 100px;
}

.board-cell {
  font-size: 25px;
  text-align: center;
  user-select: none;
  height: 30px;
  width: 30px;
}

.border-cell-name {
  border: none;
  height: 10px !important;
  width: 10px !important;
}

.names-row {
  background: #9797beb4;
}

.board-cell-active:hover {
  opacity: 0.3;
  background-color: white;
}

body {
  height: auto;
  background: "tic-tac-toe.avif";
  background-repeat: no-repeat;
  background-size: auto;
}

body::after {
  content: '';
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #9797beb4;
  z-index: -1;
}

.status {
  display: flex;
  justify-content: center;
  font-size: 25px;
  margin-top: 20px;
  color:white;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.color-white {
  color: white;
}